.button {
  padding: 0.5rem 1rem;
  min-height: 60px;
  display: flex;
  align-items: center;

  background-color: white;
  border: 1px solid black;
  border-radius: 0.5rem;
  font-size: 24px;
  cursor: pointer;
  transition: 0.25s;
}
.button:hover {
  background-color: black;
  color: white;
}
